@charset "utf-8";

@import 'bulma/sass/utilities/initial-variables';

$widescreen-enabled: false;
$fullhd-enabled: false;
$desktop: $tablet + (2 * $gap);

$body-font-size: 18px;
$font-family-title: 'Open Sans', sans-serif;
$font-family-body: $font-family-title;
$family-primary: $font-family-body;

$orange: #FF851B;
$red: #FF4136;
$green: #2ECC40;
$olive: #3D9970;
$aqua: #7FDBFF;

@import 'bulma/bulma';

$small-gap: $gap / 2;
$blank-space-tiny: 0.5rem;

// body {
//   // background-color: #f6f5ed;
//   // min-height: 100vh;
// }

body {
  @include mobile {
    font-size: 16px;
  }
}

// remove some bulma styles
.content {
  figure {
    margin-left: 0;
    margin-right: 0;
    text-align: unset;
  }
}

.bold-links {
  a {
    font-weight: bold;
    text-transform: uppercase;
  }
}

.text-center {
  text-align: center;
}

nav.topnav {
  padding-bottom: 0.5rem;

  .brand {
    display: inline-block;
    font-size: $size-2;
    font-weight: bold;
    color: $orange;

    img {
      height: 80px;
      width: 80px;
      border-radius: 50%;
      margin-right: $small-gap;
    }
  }

  .links {
    a {
      font-size: 16px;
      &:not(:last-child) {
        margin-right: $gap;
      }

      @include mobile {
        font-size: 14px;

        &:not(:last-child) {
          margin-right: 0.75 * $gap;
        }
      }
    }
  }
}

.centered-items {
  display: flex;
  align-items: center;
}

.content {
  color: #0B2B62;

  a {
    text-decoration: underline;
  }

  .subtitle {
    font-weight: normal;
  }
}

.projects-list {
  margin-top: 0.5rem;
}

.project-in-list {
  &:not(:last-child) {
    margin-bottom: 1.5rem;
  }
  figure.image {
    margin: 0;
  }

  .title {
    margin-bottom: 0.5rem;
  }

  .links {
    margin-top: 0.5rem;
  }
}

.inline-separator {
  margin-left: $blank-space-tiny;
  margin-right: $blank-space-tiny;
  color: $grey-lighter;
}

.blank-space {
  height: 2rem;
}

.project-hero {
  .logo {
    max-height: 96px;
  }
}

